import React,{Component} from 'react';
import store from '../store.js';


class Shop extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return (<div className="shop">
			<div className="shoptitle">
				<div></div>
			 	<ul>
			 		<li><span className="iconfont icon-te"></span><span>40158040</span></li>
			 		<li><span className="iconfont icon-shou"></span><span>17()</span></li>
			 		<li><span className="iconfont icon-te"></span><span>18</span></li>
			 		<li><span className="iconfont icon-te"></span><span>303</span></li>
			 		<li><span className="iconfont icon-te"></span><span>9.9</span></li>
			 		<li><span className="iconfont icon-te"></span><span>15</span></li>
			 		<li><span className="iconfont icon-te"></span><span>5</span></li>
			 		<li><span className="iconfont icon-te"></span><span></span></li>
			 	</ul>
			</div>
			<div className="shopcontent">
				<div><p></p><p></p></div>
				<div>
					<span></span>
					<span>020-833211305{">"}</span>
				</div>
				<div>
					<span></span>
					<span>342</span>
				</div>
				<div>
					<span></span>
					<span>11:00-21:00</span>
				</div>
			</div>
			<div className="shopbottom">
				<span></span>
				<span>{'>'}</span>
			</div>
		</div>)
	}
}


export default Shop;



